.header {
    height: 60px;
    width: 100vw;
    display: flex;
    align-items: center;
    background-color: #131921;
    position: sticky;
    top: 0;
    z-index: 100;
}

.header__search {
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
}

.header__logo {
    width: 100px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 18px;
}

.header__searchInput {
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}

.header__searchIcon {
    background-color: #cd9042;
    padding: 5px;
    height: 22px !important;
}

.header__optionLIneOne {
    font-size: 10px;
}

.header__optionLineTwo {
    font-size: 14px;
    font-weight: 800;
}

.header__nav {
    display: flex;
    justify-content: space-evenly;
}

.header__option {
    color: white;
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
}

.header__shoppingCart {
    display: flex;
    padding: 5px;
    height: 22px !important;
    align-items: center;
}

.header__shoppingCart > NavLink {
    text-decoration: none;
    color: white;
}

.header__cartCount {
    margin-left: 10px;
    margin-right: 10px;
}

